import React from 'react';
import Header from 'src/components/Header.jsx';
import styles from 'src/styles/features.module.css';
import Helmet from 'react-helmet';
import Config from 'src/Config.js';

class CardList extends React.Component {
    render() {
        return (
            <div className={styles.CardList}>
                {this.props.children}
            </div>
        );
    };
};

class Card extends React.Component {
    render() {
        return (
            <div className={styles.Card}>
                <h1 className={styles.CardName}>
                    {this.props.name}
                </h1>
                <div className={styles.CardContent}>
                    {this.props.children}
                </div>
            </div>
        );
    };
};

class Features extends React.Component {
    componentDidMount() {
        if (document.documentElement.clientWidth <= 425) {
            alert('ВНИМАНИЕ! Вы зашли на сайт с телефона или устройства с маленьким экраном. Версия сайта будет не полной, для полной версии включите режим компьютера в браузере или зайдите с ПК');
        };
    };

    render() {
        return (
            <div className={styles.root}>
                <Helmet>
                    <meta name="viewport" content="width=device-width, initial-scale=1.0"/>
                    <title>
                        {Config.SiteData.Title}Возможности
                    </title>
                    <link rel="shortcut icon" href={Config.SiteData.IconURL} type="image/x-icon"/>
                </Helmet>
                <Header />
                <h1 className={styles.h}>
                    Возможности Kanami:
                </h1>
                <CardList>
                    <Card name="Административные команды">
                        Команды против всяких бяк!
                    </Card>
                    <Card name="Няшные реакции">
                        Реакции, которые добавят на ваш сервер позитива, уюта и няшности :3
                    </Card>
                    <Card name="Отображение информации">
                        Команды с информацией, которые помогут вам узнать информацию о боте, пользователе и не только :)
                    </Card>
                </CardList>
            </div>
        );
    };
};

export default Features;